<template>
  <div>
    <div class="goods">
      <div class="menu-wrapper" ref="menuWrapper">
        <ul>
          <li
            v-for="(item,index) in goods"
            :key="index"
            class="menu-item"
            :class="{'current' : currentIndex === index }"
            @click="selectMenu(index,$event)"
          >
            <span class="text border-1px">
              <span class="icon" v-show="item.type > 0" :class="classMap[item.type]"></span>
              {{ item.name}}
            </span>
          </li>
        </ul>
      </div>
      <div class="foods-wrapper" ref="foodsWrapper">
        <ul>
          <li v-for="(item,index) in goods" :key="index" class="food-list" ref="foodList">
            <h1 class="title">{{ item.name }}</h1>

            <ul>
              <li
                
                v-for="(food,index) in item.foods"
                :key="index"
                class="food-item border-1px"
                ref="foodSelect"
              >
                <div class="icon">
                  <img :src="food.icon" alt width="57" height="57">
                </div>
                <div class="content">
                  <h2 class="name">{{ food.name }}</h2>
                  <p class="desc">{{ food.description }}</p>
                  <div class="extra">
                    <span class="count">月售{{ food.sellCount }}份</span>
                    <span>好评率{{ food.rating }}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{ food.price }}</span>
                    <span class="old" v-show="food.oldPrice">￥{{ food.oldPrice }}</span>
                  </div>
                  <!-- 添加 将它抽离出来做一个组件 -->
                  <div class="cartcontrol-wrapper">
                    <cart-control :food="food" @add="addFood"></cart-control>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>

      <shop-cart
        ref="shopcart"
        :selectFood="selectFood"
        :deliveryPrice="seller.deliveryPrice"
        :minPrice="seller.minPrice"
      ></shop-cart>
    </div>
  </div>
</template>


<script>
import BScroll from "better-scroll";
import cartcontrol from "../cartcontrol/cartcontrol";
import Shopcart from "../shopcart/Shopcart";
export default {
  name: "Goods",
  data() {
    return {
      // classMap: [],
      goods: [],
      listHeight: [],
      scrollY: 0 //页面需要滚动多少
    };
  },
  props: {
    seller: {
      type: Object
    }
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i;
        }
      }
      return 0;
    },
    selectFood() {
    let foods = [];
    this.goods.forEach(good => {
      good.foods.forEach(food => {
        if (food.count) {
          foods.push(food);
        }
      });
    });
    return foods;
  }
  },
  components: {
    "cart-control": cartcontrol,
    "shop-cart": Shopcart
  },
  methods: {
    // better-scroll， 默认它会组织 touch 事件，所以在配置中需要加上如下属性就可以了
    // mouseWheel: true, click: true, tap: true ,proType: 3
    _initScroll() {
      this.menuScroll = new BScroll(this.$refs.menuWrapper, {
        click: true
      });
      this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
        click: true,
        probeType: 3
      });
      this.foodsScroll.on("scroll", pos => {
        this.scrollY = Math.abs(Math.round(pos.y));
        // console.log(this.scrollY)
      });
    },
    addFood(target) {
      this._drop(target);
    },
    

    _drop(target) {
      // 体验优化,异步执行下落动画
      this.$nextTick(() => {
        // 动画组件
        this.$refs.shopcart.drop(target);

      });
    },
    selectMenu(index, $event) {
      console.log(index);
      if (!event._constructed) {
        return;
      }
      let foodList = this.$refs.foodList;
      let el = foodList[index];
      this.foodsScroll.scrollToElement(el, 300);
    },
    // 获取商品列表的高度
    _calculateHeight() {
      let foodList = this.$refs.foodList;
      // console.log(foodList)
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    }
  },
  created() {
    this.classMap = ["decrease", "discount", "special", "invoice", "guarantee"];

    this.$http
      .get(
        "https://www.easy-mock.com/mock/5ca466a713e4cf68f04a42f7/js_fullStack/goods"
      )
      .then(res => {
        console.log(res);
        if (res.data.errno === 0) {
          this.goods = res.data.data;
          // $nextTick() 检测页面是否渲染完成，，完成才能执行里面的代码
          this.$nextTick(() => {
            this._initScroll();
            this._calculateHeight();
          });
        }
      });
  }
  
};
</script>


<style lang="stylus" scoped>
@import '../../common/stylus/mixin';

.goods {
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;

  .menu-wrapper {
    flex: 0 0 80px;
    width: 80px;
    background: #f3f5f7;

    .menu-item {
      display: table;
      height: 54px;
      width: 56px;
      padding: 0 12px;
      line-height: 14px;

      // border-top 1px solid #000
      &.current {
        position: relative;
        z-index: 10;
        margin-top: #ffffff;
        font-weight: 700;
        color: red;
      }

      .text {
        border-none();
        display: table-cell;
        width: 56px;
        vertical-align: middle;
        border-1px(rbga(7, 17, 27, 0.1));
        font-size: 12px;

        .icon {
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          margin-right: 2px;
          background-size: 12px 12px; // 其实就是100% 宽高都是 12px
          background-repeat: no-repeat;

          &.decrease {
            bg-image('decrease_3');
          }

          &.discount {
            bg-image('discount_3');
          }

          &.guarantee {
            bg-image('guarantee_3');
          }

          &.invoice {
            bg-image('invoice_3');
          }

          &.special {
            bg-image('special_3');
          }
        }
      }
    }
  }

  .foods-wrapper {
    flex: 1;

    .title {
      padding-left: 14px;
      height: 26px;
      line-height: 26px;
      border-left: 2px solid #d9dd el;
      font-size: 12px;
      color: rgb(147, 153, 159);
      background: #f3f5f7;
    }

    .food-item {
      display: flex;
      margin: 18px;
      padding-bottom: 18px;
      border-1px(rgba(7, 17, 27, 0.1));

      &:last-child {
        border: none();
        margin-bottom: 0;
      }

      .icon {
        flex: 0 0 57px;
        margin-right: 10px;
      }

      .content {
        flex: 1;

        .name {
          margin: 2px 0 8px 0;
          height: 14px;
          line-height: 14px;
          font-size: 14px;
          color: rgb(7, 17, 27);
        }

        .desc, .extra {
          line-height: 10px;
          font-size: 10px;
          color: rgb(147, 153, 159);
        }

        .desc {
          line-height: 12px;
          margin-bottom: 8px;
        }

        .extra {
          .count {
            margin-right: 12px;
          }
        }

        .price {
          font-weight: 200;
          line-height: 24px;

          .now {
            margin-right: 8px;
            font-size: 14px;
            color: rgb(240, 20, 20);
          }

          .old {
            text-decoration: line-through;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
        }

        .cartcontrol-wrapper {
          position: absolute;
          bottom: 12px;
          right: 0;
        }
      }
    }
  }
}
</style>



