import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Goods from '@/components/goods/Goods'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/goods',
      name:'Goods',
      component: Goods
    },
    {
      path: '/',
      name: 'Goods',
      component: Goods
    }
  ]
})



